html {
  height: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  height: 100%;
  font-family: Geometria, sans-serif;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: 0 auto;
  min-height: 100%;
  max-width: 1920px;
  counter-reset: counter;
  background-position: bottom center;
  background-repeat: no-repeat;
  background-size: cover;
}

.wrapper__bg {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: var(--primary);
  background-position: bottom center;
  background-repeat: no-repeat;
  background-size: 100% calc(100% - 49vh);

  @media (max-width:1470px) {
    background-size: auto calc(100% - 33vh);
  }

  @media (max-width:1100px) {
    background-size: auto calc(100% - 24vh);
  }

  @media (max-width:480px) {
    background-size: auto calc(100% - 42vh);
  }
}

.wrapper__page-bg {
  background-size: cover;

  @media (max-width:1470px) {
    background-size: cover;
  }

  @media (max-width:1100px) {
    background-size: cover;
  }

  @media (max-width:480px) {
    background-size: cover;
  }
}

.wrapper__about-bg {
  background-size: 100% calc(100% - 100vh);

  @media (max-width:1470px) {
    background-size: auto calc(100% - 100vh);
  }

  @media (max-width:1100px) {
    background-size: auto calc(100% - 100vh);
  }

  @media (max-width:480px) {
    background-size: auto calc(100% - 100vh);
  }
}

.main {
  position: relative;
  flex: 1 0 auto;
}

.footer {
  flex: 1 0 auto;
}

picture {
  display: block;
}

img {
  display: block;
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

p {
  margin: 0;
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1470px;
}

.container-fluid {
  padding-left: max(15px, calc((100% - 1440px) / 2));
  padding-right: 0;
}

.section-top {
  padding-top: 100px;
  padding-bottom: 50px;

  @media (max-width:1100px) {
    padding-top: 70px;
    padding-bottom: 35px;
  }

  @media (max-width:480px) {
    padding-top: 50px;
    padding-bottom: 25px;
  }
}

.section {
  padding-top: 50px;
  padding-bottom: 50px;

  @media (max-width:1100px) {
    padding-top: 35px;
    padding-bottom: 35px;
  }

  @media (max-width:480px) {
    padding-top: 25px;
    padding-bottom: 25px;
  }
}

.section-bottom {
  padding-top: 50px;
  padding-bottom: 100px;

  @media (max-width:1100px) {
    padding-top: 35px;
    padding-bottom: 70px;
  }

  @media (max-width:480px) {
    padding-top: 25px;
    padding-bottom: 50px;
  }
}

.stop-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.logo {
  width: 103px;
  flex-shrink: 0;

  @media (max-width:480px) {
    width: 67px;
  }
}

// extends
%btn {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 10px;
  color: var(--white);
  text-align: center;
}

// links
.link {
  @extend %btn;
  width: fit-content;
  outline-offset: 2px;
  outline: 2px solid transparent;
  transition: color 0.3s ease-in-out, outline-color 0.3s ease-in-out;

  &:focus {
    outline-color: var(--white);
  }

  &:hover {
    color: var(--red);
    outline-color: transparent;
  }
}

.link-btn {
  @extend %btn;
  background: rgba(255, 255, 255, 0.20);
  width: fit-content;
  outline-offset: 2px;
  outline: 2px solid transparent;
  transition: color 0.3s ease-in-out, outline-color 0.3s ease-in-out, background 0.3s ease-in-out;

  &:focus {
    outline-color: var(--white);
  }

  &:hover {
    color: var(--red);
    background: var(--white);
    outline-color: transparent;
  }
}

.nav-link {
  border-radius: 10px;
  color: var(--white);
  outline-offset: 2px;
  outline: 2px solid transparent;
  transition: color 0.3s ease-in-out, outline-color 0.3s ease-in-out;

  &:focus {
    outline-color: var(--white);
  }

  &:hover {
    color: var(--red);
    outline-color: transparent;
  }
}

.policy-link {
  color: var(--white);
  font-size: 14px;
  font-weight: 400;
  line-height: 26px;
  text-decoration: underline;
  outline-offset: 2px;
  outline: 2px solid transparent;
  transition: color 0.3s ease-in-out, outline-color 0.3s ease-in-out;

  &:focus {
    outline-color: var(--white);
  }

  &:hover {
    color: var(--red);
    outline-color: transparent;
  }
}

// buttons
.btn {
  @extend %btn;
  background: rgba(255, 255, 255, 0.20);
  outline-offset: 2px;
  outline: 2px solid transparent;
  transition: color 0.3s ease-in-out, fill 0.3s ease-in-out, outline-color 0.3s ease-in-out, background 0.3s ease-in-out;

  & svg {
    fill: var(--white);
    transition: fill 0.3s ease-in-out, transform 0.3s ease-in-out;
  }

  &:focus-visible {
    outline: 2px solid transparent;
  }

  &:focus {
    outline-color: var(--white);
  }

  &:hover {
    color: var(--red);
    background: var(--white);
    outline-color: transparent;

    & svg {
      fill: var(--red);
    }
  }
}

.transparent-btn {
  @extend %btn;
  background: transparent;
  outline-offset: 2px;
  outline: 2px solid transparent;
  transition: color 0.3s ease-in-out, outline-color 0.3s ease-in-out, background 0.3s ease-in-out;

  &:focus-visible {
    outline: 2px solid transparent;
  }

  &:focus {
    outline-color: var(--white);
  }

  &:hover {
    color: var(--red);
    background: var(--white);
    outline-color: transparent;
  }
}

.white-btn {
  &:hover {
    color: var(--white);
    background: var(--red);
  }
}

// titles
.second-title {
  margin: 0;
  color: var(--white);
  font-size: 36px;
  font-weight: 900;
  line-height: normal;

  @media (max-width:720px) {
    font-size: 30px;
  }

  @media (max-width:480px) {
    font-size: 24px;
  }
}

.third-title {
  margin: 0;
  color: var(--white);
  font-size: 30px;
  font-weight: 800;
  line-height: 42px;

  @media (max-width:720px) {
    font-size: 24px;
    line-height: normal;
  }

  @media (max-width:480px) {
    font-size: 20px;
    line-height: 26px;
  }
}

.page-third-title {
  font-weight: 700;
  line-height: 40px;

  @media (max-width:720px) {
    line-height: normal;
  }

  @media (max-width:480px) {
    line-height: 26px;
  }
}

.fourth-title {
  margin: 0;
  color: var(--white);
  font-size: 22px;
  font-weight: 700;
  line-height: 25px;

  @media (max-width:720px) {
    font-size: 20px;
  }

  @media (max-width:480px) {
    font-size: 18px;
  }
}

.numbered-title {
  &::after {
    content: "";
    position: absolute;
    top: 50%;
    left: 37px;
    width: 52px;
    height: 1.5px;
    background-color: var(--white);
    transform: translateY(-50%);
  }

  &::before {
    counter-increment: counter;
    content: counter(counter);
    position: absolute;
    top: 50%;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    width: 26px;
    height: 26px;
    color: var(--white);
    text-align: center;
    font-family: Inter;
    font-size: 10px;
    font-weight: 700;
    line-height: normal;
    background-color: rgba(255, 255, 255, 0.20);
    transform: translateY(-50%);
  }
}

// text
.text-block {
  margin-bottom: 60px;

  @media (max-width:1470px) {
    margin-bottom: 48px;
  }

  @media (max-width:1100px) {
    margin-bottom: 36px;
  }

  @media (max-width:720px) {
    margin-bottom: 32px;
  }

  @media (max-width:480px) {
    margin-bottom: 24px;
  }
}

.text {
  color: var(--white);
  font-size: 16px;
  font-weight: 400;
  line-height: 26px;

  @media (max-width:720px) {
    line-height: 24px;
  }

  @media (max-width:480px) {
    font-size: 14px;
    line-height: 22px;
  }

  &--transparent {
    color: rgba(255, 255, 255, 0.7);
  }

  &__paragraph {
    &:not(:last-child) {
      margin-bottom: 26px;

      @media (max-width:1470px) {
        margin-bottom: 24px;
      }

      @media (max-width:1100px) {
        margin-bottom: 16px;
      }

      @media (max-width:480px) {
        margin-bottom: 12px;
      }
    }

    &--bold {
      font-style: italic;
      font-weight: 500;
    }
  }

  &__link {
    color: rgba(255, 255, 255, 0.7);
    font-size: 16px;

    @media (max-width:720px) {
      line-height: 24px;
    }

    @media (max-width:480px) {
      font-size: 14px;
      line-height: 22px;
    }
  }
}

// slider
.swiper-btns {
  display: flex;
  align-items: center;
  gap: 11px;
  width: 78px;
  height: 34px;

  @media (max-width:480px) {
    width: 67px;
    height: 28px;
  }
}

.swiper-button-prev,
.swiper-button-next {
  position: static;
  border-radius: 50%;
  margin: 0;
  width: 33px;
  height: 33px;
  outline-offset: 2px;
  outline: 2px solid transparent;
  transition: color 0.3s ease-in-out, outline-color 0.3s ease-in-out, background-color 0.3s ease-in-out;

  @media (max-width:480px) {
    width: 28px;
    height: 28px;
  }

  &:focus-visible {
    outline: 2px solid transparent;
  }

  &:focus {
    outline-color: var(--white);
  }
}

.swiper-button-prev.swiper-button-disabled,
.swiper-button-next {

  & .prev-active,
  .next {
    opacity: 0;
    visibility: hidden;
    width: 0;
    height: 0;
    transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
  }

  & .next-active,
  .prev {
    opacity: 1;
    visibility: visible;
    width: 100%;
    height: 100%;
    transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
  }
}

.swiper-button-next.swiper-button-disabled,
.swiper-button-prev {

  & .next-active,
  .prev {
    opacity: 0;
    visibility: hidden;
    width: 0;
    height: 0;
    transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
  }

  & .prev-active,
  .next {
    opacity: 1;
    visibility: visible;
    width: 100%;
    height: 100%;
    transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
  }
}

.swiper-button-prev::after,
.swiper-button-next::after {
  display: none;
}

.swiper-button-prev {
  transform: rotateY(180deg);
}

// cards
.gift-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 4px inset #d9d9d9;
  border-radius: 10px;
  width: 100%;
  background-color: var(--white);
  outline-offset: 2px;
  outline: 2px inset transparent;
  transition: outline-color 0.3s ease-in-out;

  &:focus-visible {
    outline: 2px inset transparent;
  }

  &:focus {
    outline-color: #d9d9d9;

    &.gift-card--active {
      outline-color: var(--red);
    }
  }

  &:hover {
    outline-color: transparent;

    & .gift-card__title {
      color: var(--red);
    }

    &.gift-card--active {
      outline-color: transparent;
    }
  }

  &--active {
    border: 4px inset var(--red);
  }
}

.gift-card__title {
  display: inline-block;
  color: var(--dark);
  font-size: 16px;
  font-weight: 500;
  text-wrap: balance;
  transition: color 0.3s ease-in-out;

  @media (max-width:480px) {
    font-size: 14px;
  }
}

.gift-card__price {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: auto;
  color: var(--dark);

  & svg {
    flex-shrink: 0;
    width: 19px;
    height: 26px;
  }
}

// forms
.input {
  border: none;
  border-radius: 10px;
  color: var(--dark);
  outline-offset: 2px;
  outline: 2px solid transparent;
  transition: color 0.3s ease-in-out, outline-color 0.3s ease-in-out;
  -webkit-appearance: none;
  appearance: none;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }

  &:focus-visible {
    outline: 2px solid transparent;
  }

  &:focus {
    outline-color: var(--white);
  }

  &:hover {
    color: var(--red);
    outline-color: transparent;

    &::placeholder {
      color: var(--red);
    }
  }

  &::placeholder {
    color: #7b7b7b;
    transition: color 0.3s ease-in-out;
  }
}

.form-label-checkbox {
  position: relative;
  color: var(--white);
  font-size: 14px;
  font-weight: 400;
  line-height: 26px;

  @media (max-width:720px) {
    line-height: 22px;
  }
}

.form-input-checkbox {
  position: absolute;
  z-index: -1;
  opacity: 0;

  &:focus+.form-checkbox {
    outline-color: var(--white);
  }

  &:hover+.form-checkbox {
    outline-color: transparent;
  }

  &:checked+.form-checkbox {
    background-position: center center;
    background-image: url("../images/svg/form-check-mark-light.svg");
  }
}

.form-checkbox {
  position: absolute;
  left: 0;
  top: 0;
  display: inline-block;
  flex-shrink: 0;
  flex-grow: 0;
  border: none;
  border-radius: 5px;
  width: 26px;
  height: 26px;
  background: rgba(255, 255, 255, 0.2);
  background-repeat: no-repeat;
  cursor: pointer;
  outline: 1px solid transparent;
  outline-offset: 1px;
  transition: outline-color 0.3s ease-in-out;
}

.form-label-checkbox-text {
  display: inline-block;
  padding-left: 40px;
  cursor: pointer;
  user-select: none;
}

// simplebar
.simplebar-track.simplebar-vertical {
  width: 5px;
}

.simplebar-scrollbar:before {
  left: 0;
  right: 0;
  border-radius: 0;
  background: var(--white);
}
